/* stylelint-disable */
/* stylelint-enable */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswald-400.woff2") format("woff2"), url("../fonts/oswald-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswald-500.woff2") format("woff2"), url("../fonts/oswald-500.woff") format("woff");
}
:root {
  --black: #000000;
  --black-2: #111111;
  --black-3: #222222;
  --grey: #444444;
  --grey-2: #666666;
  --grey-3: #cdcdcd;
  --grey-4: #dcdcdc;
  --grey-5: #f2f2f2;
  --grey-6: #d9d9d9;
  --white: #ffffff;
  --white-2: #e6e6e6;
  --green: #68b738;
  --green-2: #5eaa2f;
  --green-3: #6ba72e;
  --orange: #ff8282;
}

body {
  min-width: 320px;
  margin: 0 auto;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: var(--grey);
  background-color: var(--white);
}

*,
::before,
::after {
  box-sizing: border-box;
}

img {
  display: block;
}

.show-main-nav {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  box-sizing: border-box;
  min-width: 320px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-right: 110px;
    padding-left: 110px;
  }
}

.button {
  padding: 7px 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: var(--white);
  text-transform: uppercase;
  text-decoration: none;
  background-color: var(--green);
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}

.button--transparent {
  color: var(--grey);
  background-color: transparent;
}

.social-list {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  column-gap: 25px;
}

.social-list__link:hover {
  fill: var(--green);
}

.social-list__link:active {
  opacity: 0.3;
}

.page__title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  font-family: "Oswald", "Arial", sans-serif;
  color: var(--black);
}
@media (min-width: 768px) {
  .page__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.page__sub-title {
  font-size: 24px;
  line-height: 24px;
  color: var(--black);
}

.site-navigation {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .site-navigation {
    display: flex;
    column-gap: 16px;
  }
}

.site-navigation--open {
  position: fixed;
  top: 65px;
  right: 0;
  left: 0;
  z-index: 1;
  display: block;
  text-align: center;
  background-color: var(--white);
}

.site-navigation--open .site-navigation__item {
  padding-top: 23px;
  padding-bottom: 20px;
  border-top: 1px solid var(--white-2);
}

.site-navigation__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--black);
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .site-navigation__link {
    position: relative;
    display: flex;
    padding: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .site-navigation__link {
    font-size: 20px;
    line-height: 30px;
    color: var(--white);
  }
}

.site-navigation__link:hover {
  opacity: 0.6;
}

.site-navigation__link:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .site-navigation__link--current::before {
    content: "";
    position: absolute;
    bottom: 0;
    display: block;
    width: calc(100% - 20px);
    height: 2px;
    background-color: var(--green);
  }
}
@media (min-width: 1440px) {
  .site-navigation__link--current::before {
    bottom: 4px;
    background-color: var(--white);
  }
}

